var imageRefresh = function() {
    //parallax scroll
    $('main').scroll(function() {
        //console.log('scrolling main');
        // parallaxing
        var $movebg = $('main').scrollTop() * -0.3;
        $('main .content .header .bg').css('background-positionY', ($movebg) + 'px');
    });


    $(document).ready(function() {
        $('.header .bg').fadeIn(3000);
    });



    //click event for cards
    // $('.card').click(function() {
    //     var currElem = $(this);
    //     $('.card .project_picture').each(function(index, elem) {
    //         $(elem).removeClass('active');
    //     }).promise().done(function() {
    //         currElem.find('.project_picture').addClass('active');
    //     });
    // });



    //image loaded

    $('subnav').imagesLoaded().progress(function(instance, image) {
        //image.img.parentNode.parentNode.className = image.isLoaded ? 'project_picture' : 'is-broken';
        if(image.isLoaded) {
            $(image.img).parent().parent().removeClass('is_loading');
        }
    });

    var headerImage = $('.header .bg');
    headerImage.imagesLoaded({
        background: true
    }).progress(function(instance, image) {
        //console.log('done');
        if (image.isLoaded) {
            headerImage.addClass('is_loaded');
        }
    });
}

imageRefresh();



//click event for navigation on mobile
function showSubNav(type) {
    console.log('showing sub nav');
    //$('subnav#'+type).fadeIn(300);
    $('subnav#'+type).animate({
        val: -100
    }, {
        step: function(now, fx) {
            var math = '-'+(now + 100);
            //console.log(math, now);
            $(this).css('transform', "translate3d(0, " + math + "%, 0)");
        },
        duration: '200'
    }, 'ease-in');
}


function hideSubNav(type) {
    $('subnav#'+type).animate({
        val: 0
    }, {
        step: function(now, fx) {
            var math = '-' + (now + 100);
            //console.log(math, now);
            $(this).css('transform', "translate3d(0, " + math + "%, 0)");
        },
        duration: '200'
    }, 'ease-in');
}
